import './App.css';
import { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { BiLoaderAlt } from 'react-icons/bi';
import { SiTwitter } from 'react-icons/si';
import { SiDiscord } from 'react-icons/si';
import FlyFrogs from './artifacts/contracts/FlyFrogs.sol/FlyFrogs.json';
import frog1 from './images/burger.png';
import frog115 from './images/115.png';
import frog201 from './images/201.png';
import frog241 from './images/241.png';
import frog79 from './images/79.png';
import frog100 from './images/100.png';
import frog91 from './images/91.png';

// Update with the contract address logged out to the CLI when it was deployed
const flyfrogsAddress = "0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc";
const openSeaLink = "https://opensea.io/";
const etherscanLink = "https://etherscan.io/token/0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc?a=";

function App() {
  // store quantity in local state
  const [quantity, setQuantity] = useState(1);
  const [minting, setMinting] = useState(false);
  const [frogIds, setFrogIds] = useState([]);
  const [account, setAccount] = useState();
  const [isPaused, setIsPaused] = useState(false);
  const [totalMinted, setTotalMinted] = useState(0);
  const [statusAlert, setStatusAlert] = useState();
  
  useEffect(() => {
    getIsPaused();
  }, []);
  
  // request access to the user's MetaMask account
  async function getAccount() {
    if (typeof window.ethereum !== 'undefined') {
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      setAccount(account);
      getFlyFrogs();
    }
  }
  
  async function getIsPaused() {
    const provider = ethers.getDefaultProvider();
    const contract = new ethers.Contract(flyfrogsAddress, FlyFrogs.abi, provider);
    try {
      const paused = await contract.isPaused();
      const totalSupply = await contract.totalSupply();
      setIsPaused(paused);
      setTotalMinted(totalSupply.toString());
    } catch (e) {
      console.log(e);
    }
  }

  async function getFlyFrogs() {
    if (typeof window.ethereum !== 'undefined') {
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(flyfrogsAddress, FlyFrogs.abi, provider);
      let frogIds = [];
      try {
        const balance = await contract.balanceOf(account);
        for (let tokenIndex = 0; tokenIndex < balance; tokenIndex++) {
            const tokenId = await contract.tokenOfOwnerByIndex(account, tokenIndex);
            frogIds.push(tokenId.toString());
        }
      } catch (e) {
        console.log(e);
      }
      setFrogIds(frogIds);
    }
  }

  async function mintFrogs() {
    if (typeof window.ethereum !== 'undefined') {
      setMinting(true);
      const [account] = await window.ethereum.request({ method: 'eth_requestAccounts' });
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const contract = new ethers.Contract(flyfrogsAddress, FlyFrogs.abi, signer);
      const total = (quantity * 0.022).toString();
      try {
        const transaction = await contract.mint(account, quantity, {value: ethers.utils.parseEther(total)});
        await transaction.wait();
        const totalSupply = await contract.totalSupply();
        setStatusAlert(null);
        setTotalMinted(totalSupply.toString());
      } catch (e) {
        setStatusAlert(`Error: ${e.message}`);
        setMinting(false);
      }
      setMinting(false);
      getFlyFrogs();
    } else {
      setStatusAlert('Please connect with MetaMask.');
    }
  }
  
  const renderFrog = (frog, src, includeName) => {
    return (
      <li key={frog}>
        <a href={openSeaLink+'assets/'+flyfrogsAddress+"/"+frog} target="_blank" rel="noreferrer" className="App-link">
          <img src={src} alt={"Fly Frog "+frog} width="200" />
          {includeName ? `Fly Frog ${frog}` : null}
        </a>
      </li>
    )
  }
  
  const  renderFrogs = frogs => {
    let message = `Congrats! You are the proud owner of ${frogs.length} Fly Frog.`
    if (parseInt(frogs.length) > 1) {
      message = `Congrats! You are the proud owner of ${frogs.length} Fly Frogs.`
    }
    const images = frogs.map((x) => renderFrog(x, "https://gateway.pinata.cloud/ipfs/QmTubGm6aKcjgTutgk7whbhXKeW42dHQmdXwp4GbbWxtTF/flyfrog"+x+".png", true));
    return (
      <div className="Frog-pond">
        <p>
          {message} <a className="App-link" href={etherscanLink+account} target="_blank" rel="noreferrer">View on Etherscan â†’</a>
        </p>
        <ul className="Frog-pond-list Frog-img-list">{images}</ul>
      </div>);
  }
  
  const getQuantityContent = max => {
    let content = [];
    for (let i = 1; i <= max; i++) {
      content.push(<option key={i} value={i}>{i}</option>);
    }
    return content;
  }

  return (
    <div className="App">
      <header className="App-header">
        <div className="App-container">
          <div className="flex">
            <div className="flex-item">
              <button onClick={getAccount}>{account ? account.substring(0, 6) + '...' + account.substring(38) : 'Connect Wallet'}</button>
              <h1 className="App-heading">Fly Frogs</h1>
              <p>
                The future is amphibious, ribbit.<br />
                <strong>10,000 Frogs minted!</strong>
              </p>
            </div>
            <div className="flex-item">
              <img src={frog1} alt="Fly Frog with burger hat" />
            </div>
          </div>
        </div>
      </header>
      <section className="App-mint">
        <div className="App-container">
            <div>
                {statusAlert
                  ? <p className="error">
                      {statusAlert}<br />
                      <a href="http://discord.gg/kfp4xftqKM" target="_blank" className="App-link" rel="noreferrer">Hop over to our Discord for support.</a>
                    </p>
                  : null
                }
                <div>Sold out! Thanks for the support. Purchase on <a href="https://opensea.io/collection/fly-frogs" target="_blank" rel="noreferrer">OpenSea</a>, ribbit.</div>
              </div>
          {frogIds.length > 0 ? renderFrogs(frogIds) : ''}
        </div>
      </section>
      <section className="App-about">
        <div className="App-container">
          <h2>Come on get hoppy!</h2>
          <p>
            Fly Frogs are a collection of 10,000 randomly generated NFTs. No two frogs are exactly alike. The frogs are composed of 180+ hand-drawn assets across 6 traits:
          </p>
          <ul>
            <li>backgrounds (25)</li>
            <li>skins (23)</li>
            <li>heads (52)</li>
            <li>eyes (14)</li>
            <li>mouths (17)</li>
            <li>outfits (56)</li>
          </ul>
          <p>It costs 0.022 ETH + gas to mint a Fly Frog. To mint, connect your <a href="https://metamask.io/" target="_blank" className="App-link" rel="noreferrer">MetaMask</a> wallet. Limit 4 frogs per transaction, but mint as many times as you want. Fly Frogs will be revealed instantly after minting. You can view all the minted frogs on <a href="https://opensea.io/collection/fly-frogs" className="App-link" target="_blank" rel="noreferrer">OpenSea</a>.</p>
          <p>Hop on over to <a href="https://twitter.com/FlyFrogsNFT" target="_blank" className="App-link" rel="noreferrer">Twitter</a> and <a href="http://discord.gg/kfp4xftqKM" target="_blank" className="App-link" rel="noreferrer">Discord</a> to join the Fly Frog community!</p>
          <h2>Roadmap</h2>
          <ul>
            <li>&#x1f438; 1k minted - 1 ETH giveaway, split between holders of the 10 rarest generated frogs to date!</li>
            <li>&#x1f438; 2.5k minted - Stay tuned...</li>
            <li>&#x1f438; 5k minted - Stay tuned...</li>
            <li>&#x1f438; 7.5k minted - 2 ETH giveaway, split between holders of the 10 rarest generated frogs to date!</li>
            <li>&#x1f438; 10k minted - The pond is unleashed! Let's go wild! Shirts, hats, 3d prints, what else!?!</li>
            <li>&#x1f4b8; We&rsquo;ll give 5% of profits from initial sales to <a href="http://savethefrogs.com" className="App-link" target="_blank" rel="noreferrer">Save the Frogs</a>. We&rsquo;re hoping to sell out to maximize our donation, which will make for lots of hoppy frogs!</li>
          </ul>
          <ul className="Frog-img-list">
            {renderFrog('79', frog79, false)}
            {renderFrog('115', frog115, false)}
            {renderFrog('241', frog241, false)}
            {renderFrog('100', frog100, false)}
            {renderFrog('201', frog201, false)}
            {renderFrog('91', frog91, false)}
          </ul>
        </div>
      </section>
      <footer className="App-footer">
        <div className="App-container">
          <a href="https://etherscan.io/address/0x31d4Da52c12542AC3D6aAdbA5ed26a3A563a86Dc" target="_blank" rel="noreferrer">Smart Contract</a>
          <a href="https://twitter.com/FlyFrogsNFT" target="_blank" rel="noreferrer"><SiTwitter /></a>
          <a href="http://discord.gg/kfp4xftqKM" target="_blank" rel="noreferrer"><SiDiscord /></a>
          <a href="https://opensea.io/collection/fly-frogs" target="_blank" rel="noreferrer">OpenSea</a>
        </div>
      </footer>
    </div>
  );
}

export default App;